.home-left {
    display: flex;
    width: 40%;
    flex-direction: column;
    margin-left: 10%;
}

.home {
    padding-top: 100px;
    width: 100%;
    display: flex;
    background-color: #155658ab;
    justify-content: space-evenly;
    align-items: center;
}

.frontend {
    display: flex;
    flex-direction: column;
    color: #f5ce12;
    font-size: 60px;
    padding: 35px 0;
}

.home-title {
    font-size: 35px;
    color: #efefcd3d;
}

.some-text {
    width: 60%;
    font-size: 20px;
    color: #efefcdfd;
    padding-bottom: 100px;
}

.home-right {
    width: 40%;
    justify-content: center;
    align-items: center;
    padding-bottom: 5%;
}

.divforimage {
    margin: 0 auto;
    display: flex;
    justify-content: center;
    justify-content: center;

}

.imageofme {
    width: 50%;
    background-color: #f5cf12bd;
    border-radius: 45%;
    opacity: 0.9;

}