.header {
    background-color: #056070;
    width: 30%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 20px 0;
    height: 100vh;
}

.less {
    color: #8dbc20;
    font-size: 45px;
}

.more {
    color: #8dbc20;
    font-size: 45px;
}

.me {
    color: #f5ce12;
    font-size: 45px;
}

.headerLogo {
    display: flex;
    justify-content: center;

}

.header-ul {
    flex-direction: column;
    font-size: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #efefcd;
    margin: 100% 0;


    li {
        padding: 10%;
        letter-spacing: 10px;
    }
}

.HeaderNav {
    width: 45%;
}